.About {
    margin: 0;
    padding: 0;
    background-image: url(./wallpaper.jpg);
    width: 100%;
    height: 100vh;
    background-position: center;
    background-size: cover;
    overflow: hidden;
    color: white;
}
.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5); /* Dark background color with opacity */
}

/* Apply the animation to the .container */
.container-about {
    height: 100vh;
    width: 1000px;
    position: absolute;
    right: 4%;
    display: flex;
    flex-direction: column;
    word-spacing: 5px;
    align-items: center;
    justify-content: center;
    opacity: 0; /* Initially set opacity to 0 for the fade-in effect */
    transform: translateX(-100%); /* Initially slide in from the left */
    animation: FadeIn 1s ease forwards; /* Apply the slide and fade-in animation */
}

/* Define the slide and fade-in animation keyframes */
@keyframes FadeIn {
    from {
        opacity: 0;
        transform: translateX(-100%);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

/* Apply styles to the .About section */

h3 {
    width: 80%;
    line-height: 40px;
}